@import "../../css/shared.scss";

.p-home{
  padding: 0 px(35) px(55) px(35);

  .list-item{
    .p1{
      //display: inline-block;
      font-size: px(45);
      margin-top: 0;
      margin-bottom: px(26);
      //min-width: 50%;

      i{
        color: #ffd75e;
        font-size: px(65);
        font-style: normal;
        display: block;
      }
    }

    .p2{
      font-size: px(37);
      margin-top: 0;
      margin-bottom: 0;
      line-height: 1.6;

      i{
        display: block;
        font-style: normal;
      }
    }

    .bottom{
      text-align: right;
      margin-top: px(26);
    }
  }

  .metcalfe{
    width: 100%;
    margin-top: px(150);
    display: block;
  }

  .triangle{
    width: px(820);
    height: px(609);
    background: url('../../images/triangle.png') no-repeat;
    background-size: contain;
    margin: 0 auto px(-169) auto;
    position: relative;

    div{
      position: absolute;
      top: 0;
      right: px(-30);
      width: px(379);
      height: px(608);
      background: url('../../images/level.png') no-repeat;
      background-size: contain;

      i{
        position: absolute;
        right: px(-80);
        top: px(280);
        height: px(44);
        line-height: px(44);
        font-style: normal;
        font-size: px(38);
        color: #ecc65a;
      }
    }
  }

  .status{
    display: inline-block;
    height: px(85);
    line-height: px(85);
    padding: 0 px(33);
    border: 1px solid #743ccc;
    border-radius: px(12);
    font-size: px(37);
    margin: 0 0 px(120) px(94);

    &-stop{
      border-color: #DE443D;
    }
  }

  .input{
    margin-top: px(55);
    margin-bottom: px(162);

    .input-wrapper{
      display: inline-block;
      position: relative;
      //margin-left: px(88);

      i{
        display: block;
        position: absolute;
        width: 100%;
        height: 100%;
        //background: red;
        //opacity: 0.5;
        background: transparent;
        top: 0;
      }
    }

    input {
      width: px(572);
      height: px(92);
      background-color: rgba(90, 47, 159, .2);
      border: 1px solid #cfc0c6;
      border-radius: px(16);
      font-size: px(30);
      color: #b2b2b2;
      padding: 0 px(18);
      box-sizing: border-box;
    }

    img{
      height: px(58);
      margin-left: px(20);
      vertical-align: middle;
      font-size: px(30);
    }

  }

  .invite{
    margin-top: px(55);
    margin-bottom: px(162);
    text-align: left;
    padding: px(30);
  }

  .label{
    margin-top: px(34);

    label{
      font-size: px(40);
      width: 35%;
      text-align: right;
      display: inline-block;
      padding-right: px(20);
      box-sizing: border-box;
      line-height: px(65);
    }

    p{
      color: #ffd75e;
      font-size: px(50);
      width: 65%;
      margin: 0;
      display: inline-block;
      vertical-align: bottom;
      line-height: px(65);
    }
  }

  .content{
    text-align: center;
    margin-top: px(80);

    input {
      display: block;
      width: px(718);
      height: px(112);
      background-color: rgba(90, 47, 159, .2);
      border: 1px solid #cfc0c6;
      border-radius: px(16);
      font-size: px(37);
      color: #b2b2b2;
      padding: 0 px(38);
      box-sizing: border-box;
      margin: 0 auto 0 auto;
    }

    .c-btn{
      margin-top: px(70);
    }
  }

  .pmr{
    text-align: center;
    font-size: px(38);
    color: #ecc65a;
    margin: px(55) 0 px(30) 0;
  }

  .package-json{
    text-align: center;
    color: #ecc65a;
    font-size: 12px;
    margin-top: px(120);
  }
}
